import { Divider, Grid } from '@mui/material'
import React from 'react'
import { myDate, myTime } from '../../classes/functions'
import BaseText from '../text/BaseText'

function INvoiceNoteLine(props) {
  const dateOf = myDate.getFullDate(props.dayData.date)

  let note = props.dayData.note
  // split note around line breaks
  note = note.split("\n")
  console.log(note);

  let lineContent = "";

  if (note) {
    lineContent = (
      <Grid container sx={{ textAlign: "center", padding: "0px" }}>
        <Grid item xs={2} sx={{ borderBottom: "1px solid lightgrey", borderRight: "1px solid lightgrey" }}>
          <BaseText>{dateOf}</BaseText>
        </Grid>
        <Grid item xs={10} sx={{ borderBottom: "1px solid lightgrey", textAlign: "left" }}>
          {note.map((line) => {
            return (
              <BaseText> - {line}</BaseText>
            )
          })}
        </Grid>
      </Grid>
    )
  }

  return lineContent

}

export default INvoiceNoteLine